<template>
  <div class="search-container">
    <div class="search-input">
      <div>
        <input
                type="text"
                id="search"
                v-model="selectedPosition"
                placeholder="address, neighborhood, city, state or zip"
                v-on:focus="searchResultsVisible = true"
                v-on:blur="searchResultsVisible = false, getGeolocation()"
                ref="searchLocation"
        >
          <section class="search-results_section" v-if='searchResultsVisible'>

            <div class="location-dropdown" v-for="(savedLocation, index) in savedLocations"
                 v-bind:key="index"
            >
              <span class="current-location location-dropdown_options" v-if="index === 0"
                    v-on:click="getCurrentLocation()"
                    v-on:mousedown.prevent="searchResultsVisible = true">
                {{savedLocation}}</span>
              <span v-else
                    class="location-dropdown_options"
                    v-on:mousedown.prevent="searchResultsVisible = true">{{savedLocation}}</span>
            </div>
      </section>
      </div>
      <div v-on:click="getCurrentTime()"
                    >
        <VueCtkDateTimePicker v-model="option" :onlyTime="true"
                              :format="'hh:mm a'"
                              :formatted="'hh:mm a'"
                              :minuteInterval="10"
                              :noLabel="true"
                              :placeholder="'Choose Time'"
          />
      </div>
      <router-link
              :to="{path: '/results', query: {position: position, time: option}}"
      >
        <button><img src="data:image/png;base64,"></button>
      </router-link>
    </div>
  </div>
</template>

<script>
    import gmapsInit from '../utils/gmaps';
    import KeyService from "../KeyService";
    export default {
        name: "WelcomeSearch",
        data() {
            return {
                position: [],
                savedLocations: ['current location'],
                timeArray: ['Open Now', 'AM', 'PM'],
                searchResultsVisible: false,
                timeResultsVisible: false,
                selectedPosition: '',
                selectedTime: '',
                option:  '',
                google: ''
            }
        },
        methods: {
            getCurrentLocation () {
                if (navigator.geolocation) {
                    navigator.geolocation.getCurrentPosition(this.showPosition);
                    this.selectedPosition = 'Current Location';
                    this.$refs.searchLocation.blur();

                } else {
                    console.log("Geolocation is not supported by this browser.");
                }
            },
            getCurrentTime () {
                let usaTime = new Date().toLocaleString("en-US", {timeZone: "America/Los_Angeles"}); // for now
                usaTime = new Date(usaTime);
                console.log(this.calcTime('los angeles', '-8'));
                this.time = usaTime.getHours();
                //change all times to (if 1:00 am => 0100 10:00 pm 2200 ) if over 12 - 1200
                this.selectedTime = 'Open Now';
                this.$emit('clicked', this.time)
            },
             calcTime(city, offset) {

              let d = new Date();

              let utc = d.getTime() + (d.getTimezoneOffset() * 60000);

              let nd = new Date(utc + (3600000*offset));

              let date = nd.toLocaleString();

              let la = date.split(",");

              this.option = la[1];

              return "The local time in " + city + " is " + nd.toLocaleString();

            },
            getGeolocation ()
            {
                if (this.selectedPosition !== 'Current Location') {
                    var geocoder = new this.google.maps.Geocoder();
                    let loc;
                    geocoder.geocode({address: this.selectedPosition}, (results, status) => {
                        if (status !== 'OK' || !results[0]) {
                            throw new Error(status);
                        }
                        var myLoc = new Array();
                        myLoc[0] = results[0].geometry.location.lng();
                        myLoc[1] = results[0].geometry.location.lat();
                        loc = myLoc;
                        this.position = myLoc;
                    });
                }
            },
            showPosition(position) {
                this.position.push(position.coords.longitude);
                this.position.push(position.coords.latitude);
                this.$emit('clicked', this.position)
            }
        },
        async mounted() {
            const key = await KeyService.getKeys();
            const api_key = key[0].key;
            this.google = await gmapsInit(api_key);
        }
    }
</script>

